<template>
  <div>
    新增工单
    <el-button type="text" @click="addDialogFormVisible = true">打开嵌套表单的 Dialog</el-button>

    <el-dialog title="新增" :visible.sync="addDialogFormVisible">
      <el-form ref="addForm" :model="addForm" label-width="80px">
        <el-form-item label="活动名称">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="活动区域">
          <el-select v-model="addForm.region" placeholder="请选择活动区域">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="活动时间">
          <el-date-picker
            v-model="addForm.date"
            type="date"
            placeholder="选择日期"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy/MM/dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="活动形式">
          <el-input type="textarea" v-model="addForm.desc"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addOnSubmit">创建</el-button>
          <el-button>取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>


  </div>
</template>

<script>
  export default {
    data() {
      return {
        addDialogFormVisible: false,
        addForm: {
          name: '',
          region: '',
          date: new Date().toLocaleDateString(),
          desc: ''
        }
      };
    },
    methods: {
      addOnSubmit() {
        console.log(this.addForm,'--------------submit!');

        this.addDialogFormVisible = false
      }
    }
  };
</script>
<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
    
</style>


